@media print {
  body {
    font-weight: normal;
    font-size: 11pt;
  }
  body .table {
    width: 100%;
    border-collapse: collapse;
  }
  body .table th {
    text-align: left;
    font-weight: normal;
  }
  body .table th, body .table td {
    padding: 5pt;
    border: 1pt solid black;
  }
  body .table thead tr th {
    text-align: left;
  }
  body .table thead tr th.text-end {
    text-align: right;
  }
  body .table.table-detail-view {
    width: 50%;
  }
  body .card {
    margin-top: 6pt;
    padding: 6pt 0;
  }
  body .card .table th, body .card .table td {
    padding: 5pt 0;
    border: none;
  }
  body .card .table.table-grid-view th, body .card .table.table-grid-view td {
    padding: 5pt;
    border: 1pt solid #000;
  }
  body .card .table.table-grid-view thead tr th {
    text-align: center;
  }
  .text-center {
    text-align: center;
  }
  .text-end {
    text-align: right;
  }
}